<template>
   <div>
    <el-row>
        <el-col :span='8' class="bill" id="bill-id">
            <el-tabs>
                <el-tab-pane label="账单" >
                    <el-table border :data="Data">
                        <el-table-column label="商品" prop="name"></el-table-column>
                        <el-table-column label="数量" prop="count"></el-table-column>
                        <el-table-column label="金额" prop="price"></el-table-column>
                        <el-table-column label="操作" fixed="right">
                            <template slot-scope="scope">
                                <el-button type="text" size="small" @click="addList(scope.row)">添加</el-button>
                                <el-button type="text" size="small" @click="deleteOne(scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div class="total">
                        <span>数量 ：</span><span class="totalCount">{{totalCount}}个</span> <span>价钱 ：</span><span>{{totalMoney}}元</span>
                    </div>
                    <div class="btn">
                        <el-button type="warning" style="margin-right:40px">挂单</el-button>
                        <el-button type="danger" style="margin-right:40px" @click="deleteAll">删除</el-button>
                        <el-button type="success" @click="invoicing">结账</el-button>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="饿了么"></el-tab-pane>
                <el-tab-pane label="美团"></el-tab-pane>
            </el-tabs>               
        </el-col>
        <el-col :span="16">
             <div>
                <div class="hot">热销商品</div>
                <div>
                    <ul class="list">
                        <li v-for="item,i in oftenGoods" @click="addList(item)">
                            <span>{{item.goodsName}}</span>
                            <span class="money">¥{{item.price}}元</span>
                        </li>
                    </ul>
                </div>
                <div class="goods">
                    <el-tabs>
                        <el-tab-pane label="汉堡">
                            <div>
                                <ul class="goods-list">
                                    <li v-for="item in typeGoods1" @click="addList(item)">
                                        <div>
                                            <span><img :src="item.goodsImg" class="imgcs"/></span>
                                        </div> 
                                        <div>
                                            <span>{{item.goodsName}}</span>
                                            <span>¥{{item.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="小食">
                            <div>
                                <ul class="goods-list">
                                    <li v-for="item in typeGoods2" @click="addList(item)">
                                        <div>
                                            <span><img :src="item.goodsImg" class="imgcs"/></span>
                                        </div> 
                                        <div>
                                            <span>{{item.goodsName}}</span>
                                            <span>¥{{item.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                            <div>
                                <ul class="goods-list">
                                    <li v-for="item in typeGoods3" @click="addList(item)">
                                        <div>
                                            <span><img :src="item.goodsImg" class="imgcs"/></span>
                                        </div> 
                                        <div>
                                            <span>{{item.goodsName}}</span>
                                            <span>¥{{item.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <div>
                                <ul class="goods-list">
                                    <li v-for="item in typeGoods4" @click="addList(item)">
                                        <div>
                                            <span><img :src="item.goodsImg" class="imgcs"/></span>
                                        </div> 
                                        <div>
                                            <span>{{item.goodsName}}</span>
                                            <span>¥{{item.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </div>
        </el-col> 
        
    </el-row>
   </div>
</template>
<script scoped>
import Axios from 'axios'
export default {
    data(){
        return {
            Data:[],
            oftenGoods:[],
            typeGoods1:[],
            typeGoods2:[],
            typeGoods3:[],
            typeGoods4:[],
            totalCount:0,
            totalMoney:0          
        }
    },
    created:function(){
        Axios.get('http://jspang.com/DemoApi/oftenGoods.php')
        .then(response=>{
         
         this.oftenGoods=response.data
         
        })
         Axios.get('http://jspang.com/DemoApi/typeGoods.php')
        .then(response=>{
         console.log(response)
         this.typeGoods1=response.data[0]
         this.typeGoods2=response.data[1]
         this.typeGoods3=response.data[2]
         this.typeGoods4=response.data[3]
         
        })
    },
  mounted:function(){
      let billHeight=document.documentElement.clientHeight
      document.getElementById("bill-id").style.height=billHeight+'px';
  },
  methods:{
      addList(item){
          this.totalCount=0;
          this.totalMoney=0;
          let isHave=false;
          for(let i=0;i<this.Data.length;i++){
              if(this.Data[i].goodsId==item.goodsId){
                  isHave=true;
              }
          }
          if(isHave){
              let arr=this.Data.filter(o=>o.goodsId==item.goodsId)
             
              arr[0].count++
          }else{ 
              this.Data.push({
                  goodsId:item.goodsId,
                  name:item.goodsName,
                  price:item.price,
                  count:1
              })
              console.log(this.Data)
          }
         this.allMoney()
      },
      deleteOne(item){
          this.totalCount=0;
          this.totalMoney=0;
          if(item.count>1){
              item.count--
          }else{
              this.Data=this.Data.filter(o=>o.goodsId!=item.goodsId)
          }
          this.allMoney()
      },
      allMoney(){
          if(this.Data){
            this.Data.forEach(element => {
                this.totalCount+=element.count
                this.totalMoney+=element.price*element.count
            });
         }
      },
      deleteAll(){
          this.Data=[]
          this.totalCount=0
          this.totalMoney=0
      },
      invoicing(){
          if(this.totalCount!=0){
              this.deleteAll()
              this.$message({
                  message:'结账成功',
                  type:'success'
              })
          }else{
            this.$message({
                  message:'还没有商品可以结算'
              })  
          }
      }
  }
}
</script>

<style>
    #bill-id{
        border-right: 1px solid black;
    }
    .el-tabs__item{
        width: 175px;
    }
    .el-table th{
        text-align: center;
        font-weight: 1000;
        background: snow;
        color: black;
    }
    .btn{
        margin-top: 25px;
    }
    .hot{
        height: 40px;
        background: rgb(247, 83, 71);
        color: #fff;
        text-align: center;
        line-height: 40px;
    }
    .list{
        list-style: none;
        padding: 0;
        margin:0;
    }
    .list li{
        float: left;
        padding: 10px;
        border: 1px solid pink;
        margin:40px;
        cursor: pointer;
    }
    .goods-list{
        list-style: none;
        padding: 0;
        margin:0;
    }
    .goods-list li{
        float: left;
       padding: 10px; 
       width: 200px;
       cursor: pointer;
    }
    .money{
        color: blue;
    }
    .goods{
        clear: both;
    }
    .imgcs{
        width: 40%
    }
    .total{
        margin-top: 10px;
    }
    .totalCount{
        margin-right: 20px;
    }
</style>




